<template>
  <v-ons-page>
    <p class="intro">
      This is a kitchen sink example that shows off the Vue bindings for Onsen UI.<br><br>
    </p>

    <v-ons-card v-for="page of pages" :key="page.label"
      @click="push(page.component, page.label)"
    >
      <div class="title">{{ page.label }}</div>
      <div class="content">{{ page.desc }}</div>
    </v-ons-card>
  </v-ons-page>
</template>

<script>
import PullHook from './PullHook.vue'
import Dialogs from './Dialogs.vue'
import Buttons from './Buttons.vue'
import Carousel from './Carousel.vue'
import InfiniteScroll from './InfiniteScroll.vue'
import Progress from './Progress.vue'
import DatePicker from './DatePicker.vue'
import TreeSelect from './TreeSelect.vue'
import StarRate from './StarRate.vue'
import SignatureView from './SignatureView.vue'
import Badge from './Badge.vue'
import Input from './Input.vue'
import Tag from './Tag.vue'
import Drawer from './Drawer.vue'
import Avatar from './Avatar.vue'
import Card from './Card.vue'
import ChartJs from './ChartJs.vue'

export default {
  data () {
    return {
      pages: [
        {
          component: PullHook,
          label: 'Pull Hook',
          desc: '下拉刷新列表组件'
        },
        {
          component: Dialogs,
          label: 'Dialogs',
          desc: '提示框样式'
        },
        {
          component: Buttons,
          label: 'Buttons',
          desc: '各种各样的按钮样式'
        },
        {
          component: Carousel,
          label: 'Carousel',
          desc: '图片轮播组件'
        },
        {
          component: InfiniteScroll,
          label: 'Infinite Scroll',
          desc: '无线滚动上拉加载列表组件'
        },
        {
          component: Progress,
          label: 'Progress',
          desc: '进程提示组件'
        },
        {
          component: DatePicker,
          label: 'DatePicker',
          desc: '时间日期选择组件'
        },
        {
          component: TreeSelect,
          label: 'TreeSelect',
          desc: '自定义搜索的树形下拉组件'
        },
        {
          component: StarRate,
          label: 'StarRate',
          desc: '评分组件'
        },
        {
          component: SignatureView,
          label: 'SignatureView',
          desc: '签名生成图像组件'
        },
        {
          component: Badge,
          label: 'Badge',
          desc: '未处理任务消息提醒组件'
        },
        {
          component: Input,
          label: 'Input',
          desc: '各式各样的输入框'
        },
        {
          component: Tag,
          label: 'Tag',
          desc: '可动态编辑的tag标签'
        },
        {
          component: Drawer,
          label: 'Drawer',
          desc: 'Drawer侧边可弹出抽屉'
        },
        {
          component: Avatar,
          label: 'Avatar',
          desc: '用图标、图片或者字符的形式展示图片和数据'
        },
        {
          component: Card,
          label: 'Card',
          desc: '将信息聚合在卡片容器中展示'
        },
        {
          component: ChartJs,
          label: 'ChartJs',
          desc: 'Chart报表组件'
        }
      ]
    }
  },

  methods: {
    push (page, key) {
      this.$store.commit('navigator/push', {
        extends: page,
        data () {
          return {
            toolbarInfo: {
              backLabel: 'Home',
              title: key
            }
          }
        }
      })
    }
  }
}
</script>

<style>
.intro {
  text-align: left;
  padding: 0 22px;
  margin-top: 20px;
  font-size: 14px;
  line-height: 1.4;
  color: rgba(0, 0, 0, .54);
}

ons-card {
  cursor: pointer;
  color: #333;
}

.card__title, .card--material__title {
  font-size: 20px;
}
</style>



