<template>
  <v-ons-page>
    <custom-toolbar v-bind="toolbarInfo"></custom-toolbar>
    <div style="margin-top: 10px;margin-left: 10px">
      <el-badge :value="12" class="item">
        <el-button size="small">评论</el-button>
      </el-badge>
      <el-badge :value="100" :max="99" class="item" type="primary">
        <el-button size="small">评论</el-button>
      </el-badge>
      <el-badge :value='news' class="item" type="warning">
        <el-button size="small">回复</el-button>
      </el-badge>

      <el-dropdown trigger="click">
    <span class="el-dropdown-link">
      点我查看<i class="el-icon-caret-bottom el-icon--right"></i>
    </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item class="clearfix">
            评论
            <el-badge class="mark" :value="12" />
          </el-dropdown-item>
          <el-dropdown-item class="clearfix">
            回复
            <el-badge class="mark" :value="3" />
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div style="margin-top: 20px;margin-left: 110px">
      <el-badge is-dot class="item">数据查询</el-badge>
      <el-badge is-dot class="item">
        <el-button class="share-button" icon="el-icon-share" type="primary"></el-button>
      </el-badge>
    </div>
    <div style="text-align: center;color: #1e88e5;margin-top: 15px"><span>此插件属于element-ui组件库，需先安装element-ui库</span></div>
  </v-ons-page>
</template>

<script>
  export default {
    data () {
      return {
        news: "new",
      }
    },
  }
</script>

<style>
  .item {
    margin-top: 10px;
    margin-right: 40px;
  }
</style>




